.menu-title {
    font-family: "Gotham Pro", sans-serif;
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 2px;
    display: inline-flex;
    flex-wrap: wrap;
    align-items: baseline;
    .product-title, & > span {
        margin-right: .5rem;
    }
}

.product-mark, .product-price {
    text-transform: uppercase;
}

.product-spice {
    display: flex;
    flex-wrap: wrap;
    font-size: .825rem;
    align-items: center;
    font-weight: 200;
    width: 100%;
    margin-top: .25rem;
    img {
        margin-right: .25rem;
        max-width: 25px;
    }
}

.product-mark {
    font-weight: 100;
    font-size: 20px;
}
.product-additives {
    font-size: 10px;
    font-weight: normal;
}
.product-item {
    .product-size {
        margin-top: .35rem;
    }
}
.product-size {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    .product-mark {
        margin-right: .5rem;
    }
}
.product-price {
    font-weight: 100;
    font-size: 20px;
    small {
        font-size: .7rem;
        letter-spacing: 0;
    }
}

.menu-description {
   opacity: .75;
   margin-top: .35rem;
   line-height: 1.5rem;
}

.menu-wrapper {
    display: -webkit-box; /* Not needed if autoprefixing */
    display: -ms-flexbox; /* Not needed if autoprefixing */
    display: flex;
    width: auto;
    & > * {
        padding: 0 1rem;
        & > * {
            margin-bottom: 2rem;
        }
    }
    .menu-item {
        h2 {
            margin: 0 0 .5rem 0;
            line-height: 3.75rem;
            @media screen and (min-width: 992px) {
                line-height: 5rem;
            }
        }
    }
}

.products-wrapper {
    .product-item {
        margin-bottom: 2rem;
        &:last-child {
            margin-bottom: 0;
        }
    }
}