body {
    background-color: #003845;
    color: #ffffff;
    font-family: "Gotham Pro", sans-serif;
    font-weight: 100 !important;
    font-size: 1rem;
}

* {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
    font-family: "DELIRIUM NEON NCV", sans-serif;
    color: #ed0678;
    font-weight: normal;
}

h1, h2 {
    font-size: 4rem;
    line-height: 4.25rem;
	margin: 2rem 0;
    @media screen and (min-width: 767px) {
        font-size: 60px;
    }
    @media screen and (min-width: 992px) {
        font-size: 90px;
    }
}

a {
    color: #ffffff;
	&:hover {
		text-decoration: none;
	}
}

#menu-switch {
	margin-bottom: 2rem;
}

.container {
    max-width: 1270px;
    width: 100%;
    margin: 0 auto;
    position: relative;
}

.aligncenter {
	text-align: center;
}

.row {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    & > * {
        padding: 0 1rem;
    }
    .col {
        flex: 1;
    }
    .half {
        @media screen and (min-width: 767px) {
            flex: 0 0 50%;
            width: 50%;
        }
    }
}

ul {
	&.social-nav {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		svg {
			width: 45px;
			height: 45px;
		}
	}
}

#main {
    padding: 0 0 15px;
	@media screen and (min-width: 767px) {
		padding: 0 0 80px;
	}
}

.main-header {
    text-align: center;
    padding: 0 1rem;
    padding-top: 30px;
}

.main-footer {
    padding: 30px 1rem;
	.container {
		padding-left: 0;
		padding-right: 0;
	}
    ul {
        display: flex;
        flex-wrap: wrap;
        padding: 0;
        margin: 0;
        list-style: none;
        li {
            margin-right: 1rem;
        }
    }
}

.menuSwitch {
    position: fixed;
    background-color: #ed0678;
    color: #ffffff;
    right: 10px;
    bottom: 10px;
    height: 60px;
    width: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 100%;
    box-shadow: 0 0 20px rgb(0 0 0 / 50%);
    cursor: pointer;
}

.react-cookie-law-dialog {
	background-color: #000 !important;
	color: #fff !important;
	.react-cookie-law-msg {
		color: #fff !important;
	}
	a {
		color: #fff !important;
	}
}

.react-cookie-law-manage-btn, .react-cookie-law-save-btn {
	background-color: transparent !important;
	border: 0 none !important;
	padding: 0 !important;
	margin-bottom: 1rem !important;
	@media screen and (min-width: 767px) {
		margin-bottom: 0 !important;
		display: inline-flex !important;
		border: 0 none !important;
		padding: .75rem 1.15rem !important;
		text-transform: uppercase !important;
		align-items: center !important;
		cursor: pointer !important;
		background-color: transparent !important;
		color: #fff !important;
		text-decoration: none !important;
		font-weight: 600 !important;
		border: 2px solid #ed0678 !important;
		transition: .2s ease-in-out !important;
		font-family: "Gotham Pro", sans-serif !important;
		&:hover {
			background-color: #ed0678 !important;
		}
	}
}

.react-cookie-law-accept-btn {
	display: inline-flex !important;
	border: 0 none !important;
	padding: .75rem 1.15rem !important;
	text-transform: uppercase !important;
	align-items: center !important;
	cursor: pointer !important;
	background-color: #ed0678 !important;
	color: #fff !important;
	text-decoration: none !important;
	font-weight: 600 !important;
	border: 2px solid #ed0678 !important;
	transition: .2s ease-in-out !important;
    font-family: "Gotham Pro", sans-serif !important;
	&:hover {
		background-color: transparent !important;
	}
}

.react-cookie-law-button-wrapper {
	padding-top: 1rem;
}

.btn {
	display: inline-flex;
	border: 0 none;
	padding: .75rem 1.15rem;
	text-transform: uppercase;
	align-items: center;
	cursor: pointer;
	&.standard {
		background-color: #ed0678;
		color: #fff;
		text-decoration: none;
		font-weight: 600;
		border: 2px solid #ed0678;
		transition: .2s ease-in-out;
		&:hover {
			background-color: transparent;
		}
	}
	&.outline {
		background-color: transparent;
		color: #fff;
		text-decoration: none;
		font-weight: 600;
		border: 2px solid #ed0678;
		transition: .2s ease-in-out;
		&:hover {
			background-color: #ed0678;
		}
	}
	&.icon {
		padding: 0rem .85rem 0rem .25rem;
		img {
			max-width: 36px;
			position: relative;
			width: 100%;
			display: block;
		}
	}
}

